window.onload = function() {
 
  // nav animate 
  $('#navbarNav a, .navbar-brand').on('click', function() {
    console.log(1); 
    var target = $(this).attr("href");
    if (target == "home") {
      smoothScroll($(target), 0);
    } else if ($(window).width() < 768) {
      smoothScroll($(target), 0);
    } else {  
      smoothScroll($(target), -75);
    }
  }); 

  $(window).on('scroll', function() {
    if ($(this).scrollTop() > 50) {
      $('.navbar').addClass('affi');
    } else {
      $('.navbar').removeClass('affi'); 
    }   
  }); 

  function smoothScroll(target, offset) {
    if (!offset) offset = 0;
    $('body,html').animate({ 'scrollTop': target.offset().top + offset },
      500
    );
  }   

  // btn collapse trigger
  $(function() {
    $('#navbarNav a').click(function() {
      if ($(window).width() < 768) {
        $('button.navbar-toggler').trigger('click');
      }
    });
  });

  // button hover
  $('.cst-btn')
    .on('mouseenter', function(e) {
      var parentOffset = $(this).offset(),
          relX = e.pageX - parentOffset.left,
          relY = e.pageY - parentOffset.top;
      $(this).find('span').css({top:relY, left:relX})
    })
    .on('mouseout', function(e) {
      var parentOffset = $(this).offset(),
        relX = e.pageX - parentOffset.left,
        relY = e.pageY - parentOffset.top;
    $(this).find('span').css({top:relY, left:relX})
    });

}    




